<template>
  <div class="container">
    <h1 class="text-center">{{ title }}</h1>

    <div class="row block-section">
      <div v-for="item in data"
           :key="item.id"
           class="block col-lg-6 col-md-6 col-sm-6 col-xs-12">
        <h2>{{ item.title }}</h2>
        <div class="svgs">
          <i :class="item.icons"></i>
        </div>
        <p>{{ item.para }}</p>
      </div>
    </div>
    <div class="row buttons">
      <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
        <button href="https://github.com/ninalouw" class="btn btn-primary">View Github</button>
        <button class="btn btn-secondary">Download Resume</button>
        <button href="mailto:ninalouw@mac.com" class="btn btn-tertiary">Contact Me</button>
      </div>
    </div>
  </div>
</template>

<script>
import Data from '../data/data.json';

export default {
  name: 'Skills',
  data() {
    return {
      title: 'Skills & Approach',
      data: Data.Skills,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
h1, h2
  font-weight: bold
  font-family: 'Playfair Display', serif
.block-section
  margin-top: 60px
.block
  display: flex
  flex-direction: column
  align-content: center
  i
    font-size: 4rem
    // margin: 5px 5px 10px 0px
    padding-top: 2px
    color: #F7E866
    &:before
      margin: 10px
  .svgs
    margin-top: 30px
    margin-bottom: 30px

.buttons
  margin-top: 30px
  margin-bottom: 30px
  .btn
    padding: 10px 20px 10px 20px
    margin-right: 5px
    margin-top: 10px
    border-radius: 0%
    &:hover
      cursor: pointer
  .btn-primary
    background-color: #FFC8D9
    border: 1px solid #FDB0D5
  .btn-secondary
    background-color: #FFFAD0
    border: 1px solid #FDF296
    color: black
    margin-left: 95px
  .btn-tertiary
    background-color: #7EC1CC
    border: 1px solid #65CFE1
    margin-left: 95px

</style>



